import logo from './logo.svg';
import './App.css';
import axios from 'axios';
import React, {useState, useEffect} from 'react';

function App() {

  const [breed, setBreed] = useState([])
  useEffect(() => {
      axios({
      method: 'GET',
      url:'http://127.0.0.1:8000/api/breed'
      }).then(response => {
          setBreed(response.data)
      })
  },[])

  return (
    <div className="App">
     <h1>Hello React</h1>
     <ul>
         {breed.map(p =>(
             <li key={p.id}>{p.image}</li>
         ))}
     </ul>
    </div>
  );
}

export default App;
